function choosemenu() {
    var items = $('.page-template-titlewsub .menu-features .menu-types li');
    var menus = $('.page-template-titlewsub .menu-features section');


    $(items).on('click', function() {
        var id = $(this).attr("id");
        items.removeClass('active');
        menus.removeClass('active fadeIn animated');

        $(this).addClass('active');

        menus.filter(function() {
            return $(this).attr('id') === id
        }).addClass('active fadeIn animated');
    });
}

function animations() {
    $('.single-product #theme-wrapper .images').hide();

    setTimeout(function() {
        $('.single-product #theme-wrapper .images').show().addClass('animated slideInLeft');
    }, 500);

    $('.single-product #theme-wrapper .product_info .button, .fpd-modal-product-designer.fpd-modal-overlay .fpd-modal-wrapper .options .button, .fpd-modal-product-designer.fpd-modal-overlay .header .header-bar .inner .exit').click(function() {
        $('.fpd-modal-product-designer.fpd-modal-overlay .fpd-modal-wrapper .fpd-container').hide().removeClass('animated slideInUp');
        setTimeout(function() {
            $('.fpd-modal-product-designer.fpd-modal-overlay .fpd-modal-wrapper .fpd-container').show().addClass('animated slideInUp');
        }, 500);
    })

}
